.products {
    position: relative;
    flex-basis: 28%;

    img {
        display: block;
    }

    .product-image {
        display: flex;
        justify-content: center;
        position: relative;

        .action-links {
            position: absolute;
            width: 160px;
            bottom: -50px;
            z-index: 10;
            opacity: 0;
            visibility: hidden;
            transition: visibility 0s ease-in-out, opacity 0.5s ease-in-out, bottom 0.5s ease-in-out;

            .ti-eye,
            .ti-heart,
            .ti-search {
                cursor: pointer;
                border-radius: 50%;
                width: 50px;
                height: 50px;
                background: white;
                font-size: 14px;
                display: inline-flex;
                justify-content: center;
                align-items: center;
            }
        }

        &.product-image:hover .action-links {
            opacity: 1;
            bottom: 60px;
            visibility: visible;
            transition: visibility 0s ease-in-out, opacity 0.5s ease-in-out, bottom 0.5s ease-in-out;

        }
    }
}
